html {
	box-sizing: border-box;
	height: 100%;
}

*,
&:before,
&:after {
	box-sizing: inherit;
}

body {
	margin: 0;
	padding: 0;
}

.wrapper {
	overflow: hidden;
	display: table;
	width: 100%;
	height: 100%;
	height: 100vh;
	max-width: 1920px;
	margin: 0 auto;
	table-layout: fixed;
}

img {
	max-width: 100%;
	display: block;
	height: auto;
}

div[class *="logo-holder"],
div[class *="img-holder"] {
	font-size: 0;
}

.js-fine-align-landscape {
	position: absolute;
	top: 0;
	bottom: 0;
    left: -50%;
    right: -50%;
    height: 100%;
    max-width: none;
    width: auto;
    margin: auto;
}
.js-fine-align-portrait {
	position: absolute;
    left: 0;
    right: 0;
    top: -100%;
    bottom: -100%;
    height: auto;
    max-width: none;
    width: 100%;
    margin: auto;
}

.inline-block-align > * {
	display: inline-block;
	vertical-align: middle;
}

.text {
	&-center {
		text-align: center;
	}
	&-uppercase {
		text-transform: uppercase;
	}
	&-accent {
		// color: $brand-accent;
	}
}

.pull-left {
	float: left;
}
.pull-right {
	float: right;
}

.por {
	position: relative;
}